<template>
	<div class="Roles">
		<!-- 面包屑导航区域 -->
		<crumbs>
			<b slot="crumbsa">权限管理</b>
			<b slot="crumbsb">角色列表</b>
		</crumbs>
		<!-- 卡片视图 -->
		<el-card>
			<el-col :span="3">
				<el-button class="jsbutton" type="primary" @click="addRoles = true">添加角色</el-button>
			</el-col>
			<!-- 角色列表区域 -->
			<el-table border :data="rolelist">
				<!-- 展开列 -->
				<el-table-column type="expand">
					<template v-slot="scope">
						<el-row :class="['bdbottom', i1 == 0 ? 'bdtop' : '', 'vcenter']" v-for="(item1, i1) in scope.row.children" :key="item1.id">
							<!-- 渲染一级权限 -->
							<el-col :span="5">
								<el-tag closable @close="removeRightId(scope.row, item1.id)">{{item1.authName}}</el-tag>
								<i class="el-icon-caret-right"></i>
							</el-col>
							<!-- 渲染二级和三级权限 -->
							<el-col :span="19">
								<!-- 通过for循环嵌套渲染二级权限 -->
								<el-row :class="[i2 != 0 ? 'bdtop' : '', 'vcenter']" v-for="(item2, i2) in item1.children" :key='item2.id'>
									<el-col :span="6">
										<el-tag type="success" closable @close="removeRightId(scope.row, item2.id)">{{item2.authName}}</el-tag>
										<i class="el-icon-caret-right"></i>
									</el-col>
									<el-col :span="18">
										<el-tag type="warning" v-for="(item3, i3) in item2.children" :key="item3.id" closable @close="removeRightId(scope.row, item3.id)">
											{{item3.authName}}
										</el-tag>
									</el-col>
								</el-row>
							</el-col>
						</el-row>
					</template>
				</el-table-column>
				<!-- 索引列 -->
				<el-table-column align="center" label='序号' type='index'></el-table-column>
				<el-table-column align="center" label='角色名称' prop="roleName"></el-table-column>
				<el-table-column align="center" label='角色描述' prop="roleDesc"></el-table-column>
				<el-table-column align="center" label='操作' width="300px">
					<template v-slot="scope">
						<el-button @click="showEditRoles(scope.row.id)" size="mini" type="primary" icon="el-icon-edit">编辑</el-button>
						<el-button @click="removeUserId(scope.row.id)" size="mini" type="danger" icon="el-icon-delete">删除</el-button>
						<el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)">分配权限</el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-card>
		
		<!-- 添加角色对话框 -->
		<el-dialog
		  title="添加角色"
		  :visible.sync="addRoles"
		  width="50%"
			@close="addRolesClose">
			<el-form :model="RolesForm" :rules="RolesFormRules" ref="RolesFormRef" label-width="80px">
			  <el-form-item label="角色名称" prop="roleName">
			    <el-input v-model="RolesForm.roleName"></el-input>
			  </el-form-item>
				<el-form-item label="角色描述" prop="roleDesc">
				  <el-input v-model="RolesForm.roleDesc"></el-input>
				</el-form-item>
			</el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="addRoles = false">取 消</el-button>
		    <el-button type="primary" @click="addUser">确 定</el-button>
		  </span>
		</el-dialog>
		<!-- 编辑角色对话框 -->
		<el-dialog
		  title="编辑角色"
		  :visible.sync="editRoles"
		  width="50%"
			@close="editRolesClose">
			<el-form :model="editRolesForm" :rules="RolesFormRules" ref="editRolesFormRef" label-width="80px">
			  <el-form-item label="角色名称" prop="roleName">
			    <el-input v-model="editRolesForm.roleName"></el-input>
			  </el-form-item>
				<el-form-item label="角色描述" prop="roleDesc">
				  <el-input v-model="editRolesForm.roleDesc"></el-input>
				</el-form-item>
			</el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="editRoles = false">取 消</el-button>
		    <el-button type="primary" @click="editUser">确 定</el-button>
		  </span>
		</el-dialog>
		<!-- 分配权限对话框 -->
		<el-dialog
		  title="分配权限"
		  :visible.sync="setRightDialogVisible"
		  width="50%"
			@close="setRightDialogClosed">
		  <!-- 树形控件 -->
			<el-tree :data="rightslist" :props="treeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" ref="treeRef"></el-tree>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="setRightDialogVisible = false">取 消</el-button>
		    <el-button type="primary" @click="allotRights">确 定</el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script> 
	import Crumbs from '../common/Crumbs.vue'
  export default{
    name:"Roles",
		components:{
			Crumbs
		},
		data(){
			return {
				// 所有角色列表数据
				rolelist:[],
				// 控制添加角色对话框
				addRoles: false,
				RolesForm:{
					roleName:'',
					roleDesc:''
				},
				RolesFormRules:{
					roleName:[
						{ required: true, message: '请输入角色名称', trigger: 'blur' },
						{ min: 3, max: 6, message: '长度在 3 到 6 个字符', trigger: 'blur' }
					],
					roleDesc:[
						{ required: true, message: '请输入角色描述', trigger: 'blur' },
						{ min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
					]
				},
				editRolesForm:{},
				// 控制编辑角色对话框
				editRoles:false,
				// 控制分配权限对话框
				setRightDialogVisible:false,
				// 所有权限的数据
				rightslist:[],
				// 树形控件的属性绑定对象
				treeProps:{
					label:'authName',
					children:'children'
				},
				// 默认选中的节点id值数组
				defKeys:[],
				//当前即将分配权限的id
				roleId:''
			}
		},
		created() {
			this.getRolesList()
		},
		methods:{
			async getRolesList(){
				const {data:res} = await this.$http.get('roles')
					// console.log(res)
					if(res.meta.status !== 200){
						return this.$message.error('获取角色列表失败')
					}
					this.rolelist = res.data
					// console.log(this.rolelist)
			},
			addRolesClose(){
				this.$refs.RolesFormRef.resetFields()
			},
			addUser(){
				this.$refs.RolesFormRef.validate(async valid => {
					// console.log(valid)
					if(!valid) return
					const {data:res} = await this.$http.post('roles',this.RolesForm)
					// console.log(res)
					if(res.meta.status !== 201){
						return this.$message.error('添加角色失败')
					}
					this.$message.success('添加角色成功')
					this.addRoles = false
					this.getRolesList()
				})
			},
			async showEditRoles(id){
				const {data:res} = await this.$http.get('roles/' + id)
				// console.log(res)
				if (res.meta.status !== 200) {
					return this.$messag.error('查询用户信息失败')
				}
				this.editRolesForm = res.data
				this.editRoles = true
			},
			editRolesClose(){
				this.$refs.editRolesFormRef.resetFields()
				this.editRolesForm = {}
			},
			editUser(){
				this.$refs.editRolesFormRef.validate(async valid => {
					// console.log(valid)
					if (!valid) return
					// 发起修改用户信息的请求
					const {data: res} = await this.$http.put('roles/' + this.editRolesForm.roleId, {roleName: this.editRolesForm.roleName,roleDesc: this.editRolesForm.roleDesc})
					// console.log(res)
					if (res.meta.status !== 200) {
						return this.$message.error('更新用户信息失败')
					}
					//关闭对话框
					this.editRoles = false
					// 更新数据列表
					this.getRolesList()
					// 提示修改成功
					this.$message.success('更新用户信息成功')
				})
			},
			async removeUserId(id){
				// 弹框询问用户是否删除数据
				const confirmResult = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).catch(err => err)
				
				// 如果用户确认删除,则返回值为字符串 confirm
				// 如果用户取消删除,则返回值为字符串 cancel
				// console.log(confirmResult)
				if(confirmResult !== 'confirm'){
					return this.$message.info('已取消删除')
				}
				
				const {data:res} = await this.$http.delete('roles/' + id)
					if(res.meta.status !== 200){
						return this.$message.error('删除用户失败')
					}
					this.$message.success('删除用户成功')
					this.getRolesList()
			},
			// 根据id删除对应的权限
			async removeRightId(role, rightId){
				//弹框提示用户是否要删除
				const confirmResult = await this.$confirm('此操作将永久删除该权限, 是否继续?', '提示', {
				          confirmButtonText: '确定',
				          cancelButtonText: '取消',
				          type: 'warning'
				        }).catch(err => err)
						if(confirmResult !== 'confirm'){
							return this.$message.info('取消了删除')
						} 
						
					  const {data: res} =	await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
						if(res.meta.status !== 200){
							return this.$message.error('删除权限失败')
						}
						this.$message.success('删除权限成功')
						// this.getRolesList()
						role.children = res.data
			},
			// 展示分配权限的对话框
			async showSetRightDialog(role){
				this.roleId = role.id
				// 获取所有权限数据
				const {data:res} = await this.$http.get('rights/tree') 
				if(res.meta.status !== 200){
					return this.$message.error('获取权限失败')
				}
				// 获取到的权限数据保存到data中
				this.rightslist = res.data
				// console.log(this.rightslist)
				
				// 递归获取三级节点的id
				this.getLeafKey(role, this.defKeys)
				
				this.setRightDialogVisible = true
			},
			// 通过递归的形式,获取所有三级权限的id,并保存到defKeys数组中
			getLeafKey(node, arr){
				// 如果当前node节点不包含children属性,则是三级节点
				if(!node.children){
					return arr.push(node.id)
				}
				node.children.forEach(item => this.getLeafKey(item, arr))
			},
			// 监听分配权限对话框的关闭事件
			setRightDialogClosed(){
				// 清空defKeys数组的id
				this.defKeys = []
			},
			//点击为角色分配权限
			async allotRights(){
				const keys = [
					...this.$refs.treeRef.getCheckedKeys(),
					...this.$refs.treeRef.getHalfCheckedKeys()
				]
				// console.log(keys)
				const idStr = keys.join(',')
				// console.log(idStr)
				
				const {data:res} = await this.$http.post(`roles/${this.roleId}/rights`,{rids:idStr})
				if(res.meta.status !== 200){
					return this.$message.error('分配权限失败')
				}
				this.$message.success('操作成功')
				this.getRolesList()
				this.setRightDialogVisible = false
			}
			
			
		}
  }
</script>

<style lang="less" scoped>
	.jsbutton{
		margin-bottom: 0.9375rem;
	}
	.el-tag{
		margin: 0.4375rem;
	}
	.bdtop{
		border-top: 1px solid #eee;
	}
	.bdbottom{
		border-bottom: 1px solid #eee;
	}
	.vcenter{
		display: flex;
		align-items: center;
	}
</style>
