<template>
	<div class="orders">
		<!-- 面包屑导航区域 -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
			<el-breadcrumb-item><b>订单管理</b></el-breadcrumb-item>
			<el-breadcrumb-item><b>订单列表</b></el-breadcrumb-item>
		</el-breadcrumb>
		<!-- 卡片视图区域 -->
		<el-card>
			<el-row>
				<el-col :span="8">
					 <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable>
					    <el-button slot="append" icon="el-icon-search" @click="getOrderList"></el-button>
					  </el-input>
				</el-col>
				<!-- 订单列表数据 -->
				<el-table :data="orderlist" border>
					<el-table-column align="center" type="index" label="序号"></el-table-column>
					<el-table-column align="center" label="订单编号" prop="order_number"></el-table-column>
					<el-table-column align="center" label="订单价格" prop="order_price"></el-table-column>
					<el-table-column align="center" label="是否付款" prop="pay_status">
						<template v-slot="scope">
							<el-tag type="success" v-if="scope.row.pay_status == '0'">已付款</el-tag>
							<el-tag type="danger" v-else>未付款</el-tag>
						</template>
					</el-table-column>
					<el-table-column align="center" label="是否发货" prop="is_send"></el-table-column>
					<el-table-column align="center" label="下单时间" prop="create_time">
						<template v-slot="scope">
							{{scope.row.create_time | dateFormat}}
						</template>
					</el-table-column>	
					<el-table-column align="center" label="操作">
						<template v-slot="scope">
							<el-tooltip :enterable="false" class="item" effect="dark" content="修改订单地址" placement="top">
								<el-button type="primary" icon="el-icon-edit" size="mini" @click="showBox"></el-button>
							</el-tooltip>
							<el-tooltip :enterable="false" class="item" effect="dark" content="物流进度" placement="top">
								<el-button type="success" icon="el-icon-location" size="mini" @click="showProgressBox"></el-button>
							</el-tooltip>
						</template>
					</el-table-column>
				</el-table>
			</el-row>
			<!-- 分页区域	 -->
			<el-pagination
			      @size-change="handleSizeChange"
			      @current-change="handleCurrentChange"
			      :current-page="queryInfo.pagenum"
			      :page-sizes="[5, 10, 15]"
			      :page-size="queryInfo.pagesize"
			      layout="total, sizes, prev, pager, next, jumper"
			      :total="total">
			    </el-pagination>
		</el-card>
		
		<!-- 修改地址对话框 -->
		<el-dialog
		  title="修改地址"
		  :visible.sync="addressVisible"
		  width="50%"
			@close="addressFormClosed">
		  <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px">
		    <el-form-item label="省市区/县" prop="address1">
		      <el-cascader :options="cityData" v-model="addressForm.address1"></el-cascader>
		    </el-form-item>
				<el-form-item label="详细地址" prop="address2">
				  <el-input v-model="addressForm.address2"></el-input>
				</el-form-item>
			</el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="addressVisible = false">取 消</el-button>
		    <el-button type="primary" @click="addressVisible = false">确 定</el-button>
		  </span>
		</el-dialog>
		<!-- 物流进度对话框 -->
		<el-dialog
		  title="物流进度"
		  :visible.sync="progressVisible"
		  width="50%">
		  
			 <el-timeline>
			    <el-timeline-item
			      v-for="(activity, index) in progressInfo"
			      :key="index"
			      :timestamp="activity.time">
			      {{activity.context}}
			    </el-timeline-item>
			  </el-timeline>
			
		</el-dialog>
	</div>
</template>

<script>
	import cityData from './city_data2017_element.js'
	import kuaidi from './kuaidi.js'
  export default{
    name:"Orders",
		data() {
			return {
				queryInfo:{
					query:'',
					pagenum:1,
					pagesize:10
				},
				total:0,
				orderlist:[],
				addressVisible:false,
				addressForm:{
					address1:[],
					address2:''
				},
				addressFormRules:{
					address1:[
						 { required: true, message: '请选择省市区/县', trigger: 'blur' },
					],
					address2:[
						 { required: true, message: '请填写详细地址', trigger: 'blur' },
					]
				},
				cityData,
				progressVisible:false,
				kuaidi,
				progressInfo:[]
			}
		},
		created() {
			this.getOrderList()
		},
		methods:{
			async getOrderList(){
				const {data:res} = await this.$http.get('orders',{params:this.queryInfo})
				// console.log(res)
				if(res.meta.status !== 200){
					return this.$message.error('获取订单列表失败')
				}
				this.orderlist = res.data.goods
				this.total = res.data.total
				// console.log(this.orderlist)
			},
			handleSizeChange(newSize){
				this.queryInfo.pagesize = newSize
				this.getOrderList()
			},
			handleCurrentChange(newPage){
				this.queryInfo.pagenum = newPage
				this.getOrderList()
			},
			//展示修改地址的对话框
			showBox(){
				this.addressVisible = true
			},
			addressFormClosed(){
				this.$refs.addressFormRef.resetFields()
			},
			async showProgressBox(){
				// console.log(this.kuaidi[0].data)
				this.progressInfo = this.kuaidi[0].data
				// console.log(this.progressInfo)
				this.progressVisible = true
			}
			
		}
  }
</script>

<style scoped>
	.el-input{
		margin-bottom: 0.9375rem;
	}
	.el-cascader{
		width: 100%;
	}
</style>
