<template>
	<div class="params">
		<!-- 面包屑导航区域 -->
		<crumbs>
			<b slot="crumbsa">商品管理</b>
			<b slot="crumbsb">参数列表</b>
		</crumbs>
		<!-- 卡片视图区域 -->
		<el-card>
			<!-- 警告区域 -->
			<el-alert :closable="false" title="注意：只允许为第三级分类设置相关参数!" type="warning" show-icon></el-alert>
			<!-- 选择商品分类区域 -->
			<el-row class="cta_opt">
				<el-col>
					<span>选择商品分类：</span>
					<!-- 选择商品分类的级联选择框 -->
					 <el-cascader
							v-model="selectedCateKeys"
							:options="catelist"
							:props="cateProps"
							@change="handleChange"></el-cascader>
				</el-col>
			</el-row>
			<!-- teb页签区域 -->
			<el-tabs v-model="activeName" @tab-click="handleTabClick">
				<!-- 添加动态参数的面板 -->
			    <el-tab-pane label="动态参数" name="many">
						<el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible = true">添加参数</el-button>
						<!-- 动态参数表格 -->
						<el-table :data="manyTableData" border>
							<!-- 展开行 -->
							<el-table-column align="center" type="expand">
								<template v-slot="scope">
									<!-- 循环渲染tag标签 -->
									<el-tag v-for="(item, i) in scope.row.attr_vals" :key="i" closable @close="handleClose(i, scope.row)">{{item}}</el-tag>
									<!-- 添加新标签 -->
									<el-input
									  class="input-new-tag"
									  v-if="scope.row.inputVisible"
									  v-model="scope.row.inputValue"
									  ref="saveTagInput"
									  size="small"
									  @keyup.enter.native="handleInputConfirm(scope.row)"
									  @blur="handleInputConfirm(scope.row)"
									>
									</el-input>
									<el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
								</template>
							</el-table-column>
							<el-table-column align="center" label="序号" type="index"></el-table-column>
							<el-table-column align="center" label="参数名称" prop="attr_name"></el-table-column>
							<el-table-column align="center" label="操作">
								<template v-slot="scope">
									<el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.attr_id)">编辑</el-button>
									<el-button type="danger" icon="el-icon-delete" size="mini" @click="removeParams(scope.row.attr_id)">删除</el-button>
								</template>
							</el-table-column>
						</el-table>
					</el-tab-pane>
					<!-- 添加静态属性的面板 -->
			    <el-tab-pane label="静态属性" name="only">
						<el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible = true">添加属性</el-button>
						<!-- 静态属性表格 -->
						<el-table :data="onlyTableData" border>
							<el-table-column align="center" type="expand">
								<el-table-column align="center" type="expand">
									<template v-slot="scope">
										<!-- 循环渲染tag标签 -->
										<el-tag v-for="(item, i) in scope.row.attr_vals" :key="i" closable @close="handleClose(i, scope.row)">{{item}}</el-tag>
										<!-- 添加新标签 -->
										<el-input
										  class="input-new-tag"
										  v-if="scope.row.inputVisible"
										  v-model="scope.row.inputValue"
										  ref="saveTagInput"
										  size="small"
										  @keyup.enter.native="handleInputConfirm(scope.row)"
										  @blur="handleInputConfirm(scope.row)"
										>
										</el-input>
										<el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
									</template>
								</el-table-column>
							</el-table-column>
							<el-table-column align="center" label="序号" type="index"></el-table-column>
							<el-table-column align="center" label="属性名称" prop="attr_name"></el-table-column>
							<el-table-column align="center" label="操作">
								<template v-slot="scope">
									<el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.attr_id)">编辑</el-button>
									<el-button type="danger" icon="el-icon-delete" size="mini" @click="removeParams(scope.row.attr_id)">删除</el-button>
								</template>
							</el-table-column>
						</el-table>
					</el-tab-pane>
			  </el-tabs>
			
			
		</el-card>
		
		<!-- 添加参数的对话框 -->
		<el-dialog
		  :title="'添加' + titleText"
		  :visible.sync="addDialogVisible"
		  width="50%"
			@close="addDialogclosed">
		  <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
		    <el-form-item :label="titleText" prop="attr_name">
		      <el-input v-model="addForm.attr_name"></el-input>
		    </el-form-item>
			</el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="addDialogVisible = false">取 消</el-button>
		    <el-button type="primary" @click="addParams">确 定</el-button>
		  </span>
		</el-dialog>
		
		<!-- 修改参数的对话框 -->
		<el-dialog
		  :title="'修改' + titleText"
		  :visible.sync="editDialogVisible"
		  width="50%"
			@close="editdDialogclosed">
		  <el-form :model="editForm" :rules="addFormRules" ref="editFormRef" label-width="100px">
		    <el-form-item :label="titleText" prop="attr_name">
		      <el-input v-model="editForm.attr_name"></el-input>
		    </el-form-item>
			</el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="editDialogVisible = false">取 消</el-button>
		    <el-button type="primary" @click="editParams">确 定</el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script>
	import Crumbs from '../common/Crumbs.vue'
  export default{
    name:"Params",
		components:{
			Crumbs
		},
		data() {
			return {
				//商品分类列表
				catelist:[],
				//级联选择框的配置对象
				cateProps:{
					// 次级菜单的展开方式
					expandTrigger: 'hover',
					value:'cat_id',
					label:'cat_name',
					children:'children'
				},
				// 级联选择框双向绑定的数组
				selectedCateKeys:[],
				//被激活的页签名称
				activeName:'many',
				//动态参数的数据
				manyTableData:[],
				//静态属性的数据
				onlyTableData:[],
				//控制添加对话框的显示与隐藏
				addDialogVisible:false,
				// 添加参数的表单对象
				addForm:{
					attr_name:''
				},
				//添加表达的验证规则对象
				addFormRules:{
					attr_name:[
						{ required: true, message: '请输入参数名称', trigger: 'blur' }
					]
				},
				//控制修改对话框的显示与隐藏
				editDialogVisible:false,
				//修改的表单数据对象
				editForm:{},
			}
		},
		created() {
			this.getCateList()
		},
		methods:{
			//获取所有的商品分类列表
			async getCateList(){
				const {data:res} = await this.$http.get('categories')
				if(res.meta.status !== 200){
					return this.$message.error('获取商品分类失败')
				}
				this.catelist = res.data
				// console.log(this.catelist)
			},
			// 级联选择框选择项变化触发
			handleChange(){
				this.getParamsData()
			},
			// tab页签点击事件的处理函数
			handleTabClick(){
				// console.log(this.activeName)
				this.getParamsData()
			},
			//获取参数的列表数据
			async getParamsData(){
				// console.log(this.selectedCateKeys)
				//证明选中的不是三级分类
				if(this.selectedCateKeys.length !== 3){
					this.selectedCateKeys = []
					this.manyTableData = []
					this.onlyTableData = []
					return
					 // this.$message.error('只允许为第三级分类设置相关参数!')
				}
				
				// 根据所选分类id,和当前所处面板,获取对应参数
				const {data:res} = await this.$http.get(`categories/${this.cateId}/attributes`,{params: {sel:this.activeName}})
				if(res.meta.status !== 200){
					return this.$message.error('获取参数列表失败')
				}
				
				res.data.forEach(item => {
					item.attr_vals = item.attr_vals ? item.attr_vals.split(',') : []
					// 控制文本框的显示与隐藏
					item.inputVisible = false
					item.inputValue = ''
				})
				
				// console.log(res.data)
				if(this.activeName === 'many'){
					this.manyTableData = res.data
				}else{
					this.onlyTableData = res.data
				}
			},
			//监听添加对话框的关闭事件
			addDialogclosed(){
				this.$refs.addFormRef.resetFields()
			},
			//点击按钮添加参数
			addParams(){
				this.$refs.addFormRef.validate(async valid =>{
					if(!valid) return
					const {data:res} = await this.$http.post(`categories/${this.cateId}/attributes`,{attr_name: this.addForm.attr_name, attr_sel:this.activeName})
					if(res.meta.status !== 201){
						return this.$message.error('添加参数失败')
					}	
					this.$message.success('添加参数成功')
					this.addDialogVisible = false
					this.getParamsData()
				})
			},
			// 点击按钮展示修改的对话框
			async showEditDialog(attr_id){
				//查询当前参数信息
				const {data:res} = await this.$http.get(`categories/${this.cateId}/attributes/${attr_id}`,{params:{attr_sel:this.activeName}})
				if(res.meta.status !== 200){
					return this.$message.error('获取参数失败')
				}
				this.editForm = res.data
				this.editDialogVisible = true
			},
			//监听添加对话框的关闭事件
			editdDialogclosed(){
				this.$refs.editFormRef.resetFields()
				this.editForm = {}
			},
			//点击修改参数
			editParams(){
				this.$refs.editFormRef.validate(async valid =>{
					if(!valid) return
					const {data:res} = await this.$http.put(`categories/${this.cateId}/attributes/${this.editForm.attr_id}`,{attr_name:this.editForm.attr_name, attr_sel:this.activeName})
					// console.log(res)
					if(res.meta.status !== 200){
						return this.$message.error('修改参数失败')
					}
					this.$message.success('修改成功！')
					this.getParamsData()
					this.editDialogVisible = false
				})	
			},
			// 根据id删除参数数据
			async removeParams(attr_id){
				 const confirmResult = await this.$confirm('此操作将永久删除该参数, 是否继续?', '提示', {
				          confirmButtonText: '确定',
				          cancelButtonText: '取消',
				          type: 'warning'
				        }).catch(err => err)
								if(confirmResult !== 'confirm'){
									return this.$message.info('已取消删除')
								}
								
								const {data:res} = await this.$http.delete(`categories/${this.cateId}/attributes/${attr_id}`)
								if(res.meta.status !== 200){
									return this.$message.error('删除失败')
								}
								this.$message.success('删除成功')
								this.getParamsData()
			},
			// 文本框失去焦点或按下enter键触发
			async handleInputConfirm(row){
				// console.log(111)
				if(row.inputValue.trim().length == 0){
					this.inputValue = ''
					row.inputVisible = false
					return
				}
				//如果没有return，则证明输入的内容需要做后续处理
				row.attr_vals.push(row.inputValue.trim())
				row.inputValue = ''
				row.inputVisible = false
				//发起请求，保存这次操作
				this.saveAttrVals(row)
			},
			// 点击按钮展示文本输入框
			showInput(row){
				row.inputVisible = true
				//让文本框自动获得焦点
				//$nextTick方法作用：当页面上元素被重新渲染之后，才会指定回调函数中的代码
				this.$nextTick(_ => {
				  this.$refs.saveTagInput.$refs.input.focus();
				})
			},
			//删除对应的参数可选项
			handleClose(i, row){
				row.attr_vals.splice(i ,1)
				//发起请求，保存这次操作
				this.saveAttrVals(row)
			},
			//将对attr_vals 的操作保存到数据库
			async saveAttrVals(row){
				//发起请求，保存这次操作
				const {data:res} = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`,{attr_name:row.attr_name,attr_sel:row.attr_sel,attr_vals:row.attr_vals.join(',')})
				if(res.meta.status !== 200){
					return this.$message.error('修改参数失败')
				}
				this.$message.success('修改参数项成功')
			}	
				
			
			
			
		},
		computed:{
			// 如果按钮需要被禁用则返回true
			isBtnDisabled(){
				if(this.selectedCateKeys.length !== 3){
					return true
				}
				return false
			},
			//当前选中的三级分类Id
			cateId(){
				if(this.selectedCateKeys.length == 3){
					return this.selectedCateKeys[2]
				}
				return null
			},
			//动态计算标题文本
			titleText(){
				if(this.activeName === 'many'){
					return '动态参数'
				}
				return '静态属性'
			}
			
			
			
			
			
		},
  }
</script>

<style lang="less" scoped>
	.cta_opt{
		margin: 0.9375rem 0;
	}
	.el-tag{
		margin: 0.625rem;
	}
	.input-new-tag{
		width: 9.375rem;
	}
</style>
